.c-loader-wrapper
{
    width: 300px;
    margin: 15px;
}

.c-loader
{
    width: 100%;
    padding: 2px 4px;

    border-radius: $medium-radius;
    background-color: $grey;
}

.c-loader-inner
{
    font-size: $small-font;
    font-weight: bold;

    width: 64%;
    padding: 10px;

    text-align: right;

    color: $white;
    border-radius: $small-radius;
    background: $green;
}

.c-tooltips
{
    display: flex;

    justify-content: space-around;
    flex-wrap: wrap;
}

.c-tooltip
{
    font-size: $small-font;
    font-weight: bold;

    position: relative;

    width: 120px;
    height: 36px;
    margin: 15px;
    padding: 10px 5px;

    text-align: center;

    color: $white;
    border-radius: $smallest-radius;
    background: $black;
}

.c-tooltip.c--top:before
{
    position: absolute;
    top: 100%;
    left: 45%;

    width: 0;
    height: 0;

    content: '';

    border-top: 7px solid $black;
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;
}

.c-tooltip.c--left:before
{
    position: absolute;
    top: 42%;
    right: 100%;

    width: 0;
    height: 0;

    content: '';

    border-top: 5px solid transparent;
    border-right: 7px solid $black;
    border-bottom: 5px solid transparent;
}

.c-tooltip.c--right:before
{
    position: absolute;
    top: 42%;
    left: 100%;

    width: 0;
    height: 0;

    content: '';

    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-left: 7px solid $black;
}

.c-tooltip.c--bottom:before
{
    position: absolute;
    bottom: 100%;
    left: 45%;

    width: 0;
    height: 0;

    content: '';

    border-right: 5px solid transparent;
    border-bottom: 7px solid $black;
    border-left: 5px solid transparent;
}
